@import '../globalStyle.scss';

.input {
  font-size: 16px;
  margin: 2px 0px 2px 0px;
  height: 30px;
  padding: 6px;
  background: rgb(16, 46, 67);
  outline: none;
  border: none;
  color: white;
  font-family: $font;
}

textarea {
  min-height: 80px;
  resize: none;
}

.formWrapper {
  margin: auto;
  display: flex;
  flex-direction: column;
  max-width: 500px;
  background: $blue;
  padding-bottom: 40px;
}

.wrapper {
  background: $blue;
  position: relative;
  z-index: 1;
  @include triangle-header($blue);
}

.content {
  position:relative;
  z-index:2;
  padding: 0px 80px 40px 80px;
  min-width: 370px;
  h2 {
    padding: 0px 5px;
  }
}

.title {
  @include flex-center;
  color: white;
  padding-top: 80px;
  padding-bottom: 40px;
}

.titleText {
  color: white;
  border-bottom: 2px solid white;
}

.submit {
  cursor: pointer;
  background: none;
  outline: none;
  color: white;
  border: 2px solid white;
  font-size: 16px;
  width: 100px;
  margin-top: 8px;
  padding: 8px 12px 8px 12px;
  align-self: flex-end;
  transition: color 200ms ease-in-out, border 200ms ease-in-out;
  &:hover{
    color: $pink;
    border: 2px solid $pink;
  }
}

.linksWrapper {
  display: flex;
  justify-content: center;
}

@media (max-width: 680px) {
  .content {
    padding: 0px 40px 40px 40px; 
  }
}

@media (max-width: 560px) {
  .submit {
    font-size: 14px;
    padding: 6px 8px 6px 8px;
  }
}

@media (max-width: 450px) {
  .title {
    padding-top: 20px;
  }
}